.main-services-container h1 {
  text-align: center;
  font-size: 2.5rem;
}

.main-services-container-description{
  text-align: center;
  font-size: 1.1rem;
}

.services1-card-container {
  display: flex;
  flex-direction: column;
  gap: 2.5vh;
  width: 100%;
  padding: 0 50px;
}

.services1-card-container-one {
  flex: 1;
  display: flex;
  gap: 2.5%;
  flex-wrap: wrap;
}

.vision-mission {
  flex: 1;
  min-height: 30vh;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url("./Vision-Mission-Light.webp");
  background-position: center;
  background-size: auto;
  background-repeat: no-repeat;
  padding: 0 2.5%;
  transition: all 0.5s;
}

.vision-mission:hover{
    scale: 1.1;
    cursor: pointer;
    background-image: url("./Vision-Mission-Dark.webp");
}

.vision-mission h3 {
  text-align: center;
  color: #fff;
}

.vision-mission p {
  text-align: center;
  color: #fff;
  line-height: 1.5;
  font-size: 1rem;
}

