.video-background {
  position: relative;
  width: 100%;
  height: calc(100vh - 80px); /* Adjust the height as needed */
  overflow: hidden;
  display: flex;
  align-items: center;
  padding: 0 5vw;
}

#my-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.content {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 2.5vh;
  /* Add styles for your content */
}

.content h1{
    color: #fff;
}

.content h2{
    color: #fff;
}

.content button{
    background-color: #612d91;
    color: #fff;
    border: none;
    width: fit-content;
    padding: 2vh 2vw;
    font-size:small;
    cursor: pointer;
}

.content a{
  color: #fff;
  text-decoration: none;
  width: fit-content;
}