@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat";
}

:root {
  --mainColor: #fff;
  --mainColorLight: #612d91;
  --secondaryLight: #612d91;
  --textColor: #000;
  overflow-x: hidden;
}

.navbar-main-container-one{
  position: sticky;
  top: 0;
  z-index: 21;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
  position: sticky;
  top: 0;
  z-index: 20;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
}

nav a {
  margin: 0 2rem;
  color: var(--textColor);
  text-decoration: none;
}

nav a:hover {
  color: var(--secondaryLight);
  cursor: pointer;
}

header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}

.contact-button {
  background-color: #fff;
  border: 1px solid #612d91;
  width: 10vw;
  height: 6vh;
  border-radius: 5px;
}

.contact-button:hover {
  cursor: pointer;
}

.logo {
  width: 15vw;
  height: 20vh;
  margin-top: 3vh;
  display: flex;
}

.logo img {
  width: 100%;
  height: 100%;
  align-self: center;
}

.home {
  text-decoration: none;
  border-top: 1px solid transparent;
  transition: border-top 0.7s linear;
}

.home:hover {
  border-top: 1px solid #612d91;
}

.about {
  text-decoration: none;
  border-top: 1px solid transparent;
  transition: border-top 0.7s linear;
}

.about:hover {
  border-top: 1px solid #612d91;
}

.services {
  text-decoration: none;
  border-top: 1px solid transparent;
  transition: border-top 0.7s linear;
  margin-right: 0.2rem;
}

.services:hover {
  border-top: 1px solid #612d91;
}

.drop-down {
  vertical-align: middle;
  margin-left: 4px;
}

.portfolio {
  text-decoration: none;
  border-top: 1px solid transparent;
  transition: border-top 0.7s linear;
}

.portfolio:hover {
  border-top: 1px solid #612d91;
}

.contact {
  text-decoration: none;
  border-top: 1px solid transparent;
  transition: border-top 0.7s linear;
}

.contact:hover {
  border-top: 1px solid #612d91;
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
}

.dropdown.open {
  display: block;
}

