@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 90%;
    display: flex;
    align-items:flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
    /* transform: translateY(-100vh); */
    transform: translateX(100vw);
    padding-top: 7.5rem;
    padding-bottom: 3vh;
    overflow-y: scroll;
  }

  header .responsive_nav {
    transform: none;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }

  nav a {
    font-size: 1.75vh;
  }

  .contact-button {
    display: none;
  }

  .logo {
    width: 15vw;
    height: 15vh;
  }
}
