.card1-main-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e5e5e5;
  padding: 2.5% 5%;
  gap: 2rem;
}

.card1-main-container-one {
  flex: 1;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  padding: 0 12.5%;
  flex-wrap: wrap;
}

.card1-main-container-one-card1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  padding: 3% 5%;
  background-color: #fff;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.card1-main-container-one-card1:hover {
  background-color: #612d91;
  color: #fff;
  cursor: pointer;
}

.card1-main-container-one-card1 h2 {
  text-align: center;
  align-self:stretch;
}

.card1-main-container-one-card1 p {
  text-align: center;
  line-height: 1.5;
  align-self: stretch;
}

.card1-main-container-one-card1 img {
  width: 20%;
  align-self: center;
}

